.NavigationItem{
    margin: 10px 0;
    box-sizing: border-box;
    display: block;
    width: 100%;
    /* border: 1px solid red; */
    overflow: hidden;
}

.NavigationItem a{
    font-family: 'Courier New', Courier, monospace;
    font-weight: bold;
    font-size: 14px;
    color: grey;
    text-decoration: none;
    width: 100%;
    box-sizing: border-box;
    display: block;
}

.NavigationItem a:hover,
.NavigationItem a:active,
.NavigationItem a.hover {
   color: black;
}

.NavigationItem a:active,
    .NavigationItem a.active {
        color: rgb(131, 163, 13);
        text-decoration: underline;

    }

@media (min-width: 799px){
    .NavigationItem{
        margin: 0 1px;
        width: auto;
        display: flex;
        height: 80%;
        align-items: center;

    }
    
    .NavigationItem a{
        text-decoration: none;
        height: 100%;
        padding: 0 7px;
        top: 0;
        padding-top: 2px;
        color: grey;

    }
    
    .NavigationItem a:hover,
    .NavigationItem a:active,
    .NavigationItem a.hover {
        color: rgb(131, 163, 13);
        /* border-top: 3px solid rgb(131, 163, 13);
        border-bottom: 3px solid rgb(131, 163, 13); */
    }

    .NavigationItem a:active,
    .NavigationItem a.active {
        color: rgb(131, 163, 13);
    }
}