.Holder{
    padding: 10px;
    font-family: 'Courier New', Courier, monospace;
    font-size: 12px;
    /* margin-bottom: 10px; */
    margin: 25px 10px 25px 10px;
    border-radius: 35px;
    background-color: rgb(232, 247, 193);
    border: 1px solid rgb(117, 156, 10);
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    /* box-shadow: 5px 0px 8px 10px #51ac08; */
}
.Holder:hover{
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.5), 0 6px 20px 0 rgba(0, 0, 0, 0.5);

}
.InfoHolder{
    font-size: 14px;
}

.TitleHolder{
    display: flex;
    border-bottom: 1px solid greenyellow;
    justify-content: space-around;
    align-items: center;
    /* font-size: large; */
    font-weight: 600;
}
.TitleName{
    width: 55%;
    /* border: 1px solid black; */
    overflow: hidden;
}
.TitleInfo{
    width: 15%;
    /* border: 1px solid black; */
    overflow: hidden;
}
.AddressHolder{
    margin-top: 10px;
}
.AddressTitle{
    font-size: 14px;
    font-weight: 600;
    text-decoration: underline;
}