.CartItemHolder{
    display: flex;
    flex-direction: column;
    margin: 10px 10px 10px 10px;
    border-radius: 35px;
    color:  rgb(0, 0, 0);
    font-family: 'Courier New', Courier, monospace;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    background-color: rgb(232, 247, 193);
    border: 1px solid rgb(117, 156, 10);
    /* border: 1px solid red; */
    width: 90%;
}

.CartItemHolder:hover{
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.ProductInfoViewHolder{
    display: flex;
    margin-left: 3px;
}

.ProductNameHolder > p{
    padding-left: 10px;
    padding-right: 10px;
    margin-top: 10px;
    font-size: 15px;
    font-weight: bold;
}
.ProducImageViewHolder{
    width: 20%;
    margin: 10px;
    height: 110px;
}
.ProducImageHolder{
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 5px;
    height: 100%;
}
.ProducImageHolder > img{
    height: 90%;
    padding-right: 10px;
}
.InfoControlHolder{
    display: flex;
    flex-direction: column;
    width: 75%;
    margin-right: 15px;
}
.InfoViewHolder{
    display: flex;
    flex-direction: column;
    width: 100%;


}
.InfoTitleViewHolder{
    display: flex;
    flex-direction: row;
    margin-top: -8px;
}
.InfoDetailViewHolder{
    display: flex;
    flex-direction: row;
}

.TitleHolder{
    width: 33%;
    margin-top: -3px;
    padding-top: 3px;
    display: flex;
    justify-content: center;
}
.TitleHolder > p{
   margin: 1px;
   text-indent: 10px;
}
.Title{
    font-size: 11px;
    font-weight: bold;
}
.Values{
    font-size: 11px;
    font-weight: bold;
}

.CartControlViewHolder{
    margin-top: 5px;
    display: flex;
    border-top: 1px solid black; 
    height: 40px;
}
.ControlHolder{
    width: 33%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.OldPriceText{
    text-decoration: line-through;
}

