.CartHolder{
    display: flex;
    /* border: 1px solid red; */
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /* width: 400px; */
    width: 100%;
    /* border: 1px solid red; */
    padding-top: 10px;
}
.OrderButton{
    /* border: 1px solid red; */
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 10px;
    height: 30px;
    width: 160px;
    font-size: 14px;
    font-family: 'Courier New', Courier, monospace;
    box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.3), 0 6px 20px 0 rgba(0, 0, 0, 0.3);

    background-color: rgb(230, 247, 187);
    border: 1px solid rgb(117, 156, 10);
    border-radius: 30px;
    
}
.OrderButton:hover{
    background-color: rgb(215, 245, 141);
    color:rgb(41, 49, 18);
}
.OrderButton:active{
    /* background-color: rgb(195, 245, 69); */
    background-color: rgb(215, 250, 128);
    box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.1), 0 6px 20px 0 rgba(0, 0, 0, 0.1);
    color:rgb(41, 49, 18);
}
