.LoginForm{
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: space-around;
    margin-top: 20px;
    margin-bottom: 20px;
    /* background-color: red; */
}

.Square{
    width: 53%;
    /* border: 1px solid black; */
}

.FormGroup{
    position: relative;
    margin-top: 10px;
    margin-bottom: 10px;
    width: 100%;
}

.Icon{
    position: absolute;
    margin-top: 5px;
    margin-left: 5px;
    height: 80%;
    color: rgb(101, 126, 11);
}
.InputField{
    height: 30px;
    z-index: 100;
    width: 100%;
    border: 1px solid rgb(101, 126, 11);
    border-radius: 20px;
    text-indent: 35px;
    color: rgb(101, 126, 11);
}
.InputField:focus{
    box-shadow: none;
    outline: none;
}

label{
    position: absolute;
    left: 20px;
    top: 6px;
    transition: all 0.3s;
    z-index: 90;
    color:  rgb(101, 126, 11);
    font-size: .9em;
    
}
.InputField ~ label {
    left: 34px;
    top: -8px;
    background-color: white;
    font-size: 11px;
    padding: 0 5px;
    font-weight: 500;
}

@media (max-width: 499px){
    .Square{
        width: 70%;
    }
}

.AuthModeSelector{
    margin-left: 10px;
    /* width: 50%; */
    /* border: 1px solid black; */
    display: flex;
    flex-direction: row;
    align-items: center;
    height: 20px;
    /* overflow: hidden; */
}

.AuthModeSelector > h5{
    font-size: 0.7em;
    margin-left: 5px;
    color: rgb(76, 95, 9);
}
/* .AuthModeSelector > input:checked{
