.Button{
    margin: 10px 0px;
    width: 100%;
    height: 25px;
    /* border: 1px solid red; */
    background-color: rgb(107, 134, 8);
    border-radius: 15px;
    display: flex;
    justify-content: center;
    align-items: center;
    /* text-decoration: none; */
}

.AuthButton {
    text-decoration: none;
}
