.Sidedrawer{
    position: fixed;
    width: 280px;
    max-width: 60%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 200;
    /* color: white; */
    /* background-color: rgb(131, 163, 13); */
    padding: 32px 16px;
    box-sizing: border-box;
    transition: transform 0.3s ease-out;
}

@media (min-width: 799px){
    .Sidedrawer{
        display: none;
    }
}

.Open{
    transform: translateX(0);
}
.Close{
    transform: translateX(-100%);

}

.Logo {
    height: 11%;
    margin-bottom: 32px;
    padding: 0 16px;
}

@media (max-width:799px){
    .Sidedrawer{
        background-color: white;
    }
}