.AccountControlHolder{
    font-family: 'Courier New', Courier, monospace;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /* border: 1px solid red; */
    width: 100%;
    margin-top: 10px;

    /* height: 90vh; */
}

.AdddressInfoHolder{
    /* border: 1px solid black; */
    border-radius: 40px;
    margin: 15px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 20px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    background-color: rgb(232, 247, 193);
    border: 1px solid rgb(117, 156, 10);
}

.Link{
    text-decoration: none;
    font-size: 15px;
    font-weight: bold;
    color: black;
}

.AdddressInfoHolder:hover{
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.5), 0 6px 20px 0 rgba(0, 0, 0, 0.5);
}

.LogoutButtonHolder{
    border: 1px solid black;
    border-radius: 35px;
    /* color: black; */
    font-size: 14px;
    padding: 5px 20px;
    margin: 15px 0px;
    font-size: 15px;
    font-weight: bold;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    background-color: rgb(232, 247, 193);
    border: 1px solid rgb(117, 156, 10);
}

.LogoutButtonHolder:hover{
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.5), 0 6px 20px 0 rgba(0, 0, 0, 0.5);

}