.Toolbar {
    height: 65px;
    width: 100%;
    position: sticky;
    top: 0;
    left: 0;
    background-color: rgb(228, 247, 204);
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 10px;
    box-sizing: border-box;
    z-index: 100;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.4), 0 6px 20px 0 rgba(0, 0, 0, 0.4);
    border-bottom: 2px solid rgb(117, 156, 10);
    /* overflow: hidden; */
    /* border-bottom: 5px solid black; */
}

.RightGroup{
    display: flex;
    /* border: 1px solid black; */
    height: 100%;
    width: 40%;
    justify-content: center;
    align-items: center;
    margin: 5px;
    overflow: hidden;
    /* justify-content: space-evenly; */
}
/* Cart and Desktop  of RightGroup */
.Cart{
    /* height: 100%; */
    /* height: 100px; */

    display: flex;
    align-items: center;
    justify-content: center;

    /* border: 1px solid red; */
}
/* Cart and Desktop  of RightGroup */
.CartUnits{
    position: absolute;
    font-size: 14px;
    font-weight: bold;
    /* font-weight: 700; */
    /*text-indent: 7px;*/
    margin-top: 0px; 
    color: white;
    font-family: 'Courier New', Courier, monospace;
}

.MobileOnly{
    height: 100%;
}

.SearchForm{
    width: 60%;
    margin: 0 10px;
    padding-right: 5px;
    height: 50%;
    display: flex;
    flex-direction: row;
    align-items: center;
    /* justify-content: center; */
    border: 1px solid rgb(131, 163, 13);
    border-radius: 25px;
    /* border: 1px solid red; */
}
.InputForm {
    width: 100%;
    left: 0;
    margin-right: 10px;
}
.SearchButton{
    margin: 0px 5px;
    margin-left: 10px;
    border-radius: 20px;
    height: 24px;
    width: 24px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    color: rgb(131, 163, 13);
}




.DesktopOnly{
    height: 30px;
}
