@media (max-width: 799px){
    .DesktopOnly{
        display: none;
    }
    .MobileOnly{
        margin-left: 10px;
    }

    .Toolbar {
        display: flex;
        justify-content: left;

    }
    .Cart{
        height: 27px;
    }
    .SearchForm{
        width: 70%;

    }
    .RightGroup{
        width: 70px;
    }

}