.NavigationItems{
    padding: 0 0;
    list-style: none;
    display: flex;
    flex-flow: column;
    align-items: center;
    height: 100%;
    /* margin-left: 20px; */
    margin: 0px 0px;
    white-space: nowrap;
    
}

@media (min-width: 799px){
    .NavigationItems{
        flex-flow: row;
        justify-content: left;
        font-size: 14px;
        font-weight: 250;
        font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    }
}

.horizontalRule{
    width: 100%;
    left: 0;
}
