.WishlistItemHolder{
    font-family: 'Courier New', Courier, monospace;

    border: 1px solid black;
    display: flex;
    flex-direction: column;
    border-radius: 35px;
    margin: 25px 10px 25px 10px;

    background-color: rgb(232, 247, 193);
    border: 1px solid rgb(117, 156, 10);
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);

    /* margin-top: 5px; */
    /* margin-bottom: 5px; */
}
.ProductInfoViewHolder{
    display: flex;
    margin-left: 3px;
}

.ProductNameHolder > p{

    padding-left: 15px;
    padding-right: 10px;
    /* padding-bottom: 10px; */
    margin-top: 10px;
    font-size: 13px;
    font-weight: bold;
    /* border-bottom: 1px solid black; */
}
.ProducImageViewHolder{
    width: 20%;
    margin: 10px;
    height: 110px;
}
.ProducImageHolder{
    display: flex;
    justify-content: center;
    align-items: center;
    /* border: 1px solid red; */
    height: 100%;
    margin-right: 10px;
}
.ProducImageHolder > img{
    height: 100%;
    /* padding-right: 15px; */
}
.InfoControlHolder{
    display: flex;
    flex-direction: column;
    width: 75%;
