    /* border-right: 1px solid black; */
}
.InfoViewHolder{
    display: flex;
    flex-direction: column;
    width: 100%;
    font-weight: bold;
    /* border: 1px solid cyan; */


}
.InfoTitleViewHolder{
    display: flex;
