.UserInfo {
    border: 1px solid black;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.FormHolder {
    width: 90%;
}
.Button{
    margin-top: 10px;
    border: 1px solid yellowgreen;
    display: flex;
    justify-content: center;
    margin: 10px 20%;
    height: 40px;
    border-radius: 20px;
    background-color: yellowgreen;
}
button{
    border: none;
    outline: none;
    margin: 1px;
    width: 100%;
    border-radius: 20px;
    background: yellowgreen;
}


@media(min-width: 499px) {
    .FormHolder {
        width: 400px;
    }
}
